@mixin hand-position {
  position: relative;
  z-index: $position-index;
}
@mixin center-proportions($center-size) {
  width: $center-size;
  height: $center-size;
}

@mixin center-background-proportions($center-background-size) {
  width: $center-background-size;
  height: $center-background-size;
}
